import Vue from 'vue';
import Vuex from 'vuex';
Vue.use(Vuex);

var store = new Vuex.Store({
  state:{
    isLoading:true,     //loading,true
    navList:0             //0:gameGenius 1:service 2:record 3:platform
  },
  mutations:{
    navListHandler(state,opt){    // 
        state.navList = opt.amount;
    },
    isLoadingHandler(state,opt){  // loading
        state.isLoading = opt.amount;
    }
  }
})
export default store;



